/* Theme Variables */
:root {
  /* Core Colors */
  --primary: #3b82f6;
  --primary-dark: #1d4ed8;
  --secondary: #64748b;
  --accent-blue: #60a5fa;
  --accent-indigo: #818cf8;
  --accent-cyan: #22d3ee;
  --accent-azure: #38bdf8;
  
  /* Semantic Colors */
  --success: #22c55e;
  --danger: #ef4444;
  --warning: #f59e0b;
  --info: #3b82f6;
  
  /* Background Colors */
  --bg-primary: #ffffff;
  --bg-secondary: #f8fafc;
  --bg-tertiary: #f1f5f9;
  
  /* Glass Effects */
  --glass-bg: rgba(255, 255, 255, 0.8);
  --glass-border: rgba(255, 255, 255, 0.3);
  --glass-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  
  /* Gradients */
  --gradient-blue: linear-gradient(135deg, #60a5fa, #3b82f6);
  --gradient-indigo: linear-gradient(135deg, #818cf8, #6366f1);
  --gradient-cyan: linear-gradient(135deg, #22d3ee, #0ea5e9);
  --gradient-azure: linear-gradient(135deg, #38bdf8, #0284c7);
  
  /* Border Radius */
  --border-radius: 0.5rem;
  --border-radius-sm: 0.25rem;
  --border-radius-lg: 0.75rem;
  --border-radius-xl: 1rem;
  --border-radius-2xl: 1.5rem;
  
  /* Shadows */
  --shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  --shadow-glass: 0 8px 32px rgba(59, 130, 246, 0.1);
  
  /* Fonts */
  --font-sans: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto;
  
  /* Animation Speeds */
  --transition-slow: 0.3s ease-in-out;
  --transition-medium: 0.2s ease-in-out;
  --transition-fast: 0.1s ease-in-out;
}

