}

.recommendations-list {
  padding-left: 1.5rem;
}

.recommendations-list li {
  margin-bottom: 0.75rem;
}

/* Real-time Monitoring and Video Analysis Styles */
.realtime-monitoring-container,
.video-grid {
  padding: 2rem 0;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
}

.stream-card {
  background: white;
  border-radius: var(--border-radius-lg);
  overflow: hidden;
  box-shadow: var(--shadow-md);
}

.stream-header {
  padding: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e5e7eb;
}

.stream-header h3 {
  margin: 0;
  font-size: 1.2rem;
  color: var(--dark);
}

.stream-controls {
  display: flex;
  gap: 1rem;
}

.btn-primary svg, 
.btn-danger svg,
.btn-secondary svg {
  margin-right: 0.5rem;
