/* Analytics Styles */
.filters-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  gap: 1rem;
  flex-wrap: wrap;
}

.time-range-selector {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.filter-btn {
  padding: 0.5rem 1rem;
  border: 1px solid #e5e7eb;
  background: white;
  border-radius: var(--border-radius);
  font-size: 0.875rem;
  color: var(--secondary);
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.filter-btn:hover {
  border-color: var(--primary);
  color: var(--primary);
}

.filter-btn.active {
  background: var(--primary-gradient);
  border-color: transparent;
  color: white;
}

.charts-grid {
