    transform: translateX(100px) translateY(20px) rotate(180deg) scale(0.9);
    border-radius: 20px;
  }
  75% { 
    transform: translateX(30px) translateY(50px) rotate(270deg) scale(1.05);
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  }
}

@keyframes float-complex-2 {
  0%, 100% { 
    transform: translateX(0px) translateY(0px) rotate(0deg) scale(1);
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  }
  20% { 
    transform: translateX(-40px) translateY(30px) rotate(72deg) scale(1.15);
    border-radius: 50%;
  }
  40% { 
    transform: translateX(-80px) translateY(-20px) rotate(144deg) scale(0.85);
    border-radius: 20px;
  }
  60% { 
    transform: translateX(-30px) translateY(-60px) rotate(216deg) scale(1.1);
    border-radius: 40% 60% 60% 40% / 40% 40% 60% 60%;
  }
  80% { 
    transform: translateX(20px) translateY(-30px) rotate(288deg) scale(0.95);
    border-radius: 50%;
  }
}

@keyframes float-complex-3 {
  0%, 100% { 
    transform: translateX(0px) translateY(0px) rotate(45deg) scale(1);
  }
  33% { 
    transform: translateX(-60px) translateY(-40px) rotate(165deg) scale(1.2);
  }
  66% { 
    transform: translateX(40px) translateY(-80px) rotate(285deg) scale(0.8);
  }
}

@keyframes float-complex-4 {
  0%, 100% { 
    transform: translateX(0px) translateY(0px) rotate(0deg) scale(1);
    opacity: 0.8;
  }
  25% { 
    transform: translateX(-30px) translateY(40px) rotate(90deg) scale(1.3);
    opacity: 0.6;
  }
  50% { 
    transform: translateX(50px) translateY(80px) rotate(180deg) scale(0.7);
    opacity: 1;
  }
  75% { 
    transform: translateX(80px) translateY(20px) rotate(270deg) scale(1.1);
    opacity: 0.7;
  }
}

/* Enhanced Panel Body */
.panel-body {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
  padding: 1rem;
  min-height: 300px;
}

/* Stats Container */
.stats-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  width: 100%;
}

.stat-group {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 1.5rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.stat-group h4 {
  font-size: 1.1rem;
  font-weight: 700;
  color: #ffffff !important;
  margin: 0 0 1rem 0;
  text-align: center;
}

.stat-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  font-size: 0.9rem;
}

.stat-row:last-child {
  border-bottom: none;
}

.stat-label {
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
}

.stat-value {
  color: #ffffff;
  font-weight: 700;
}

.stat-value.significant {
  color: #10b981;
}

.stat-value.decision-reject {
  color: #ef4444;
}

/* Chart Error */
.chart-error {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.9rem;
  text-align: center;
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  border-radius: 8px;
}

/* Loading State */
.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem;
  color: rgba(255, 255, 255, 0.8);
}

.pulse-loader {
  width: 40px;
  height: 40px;
  background: #3b82f6;
  border-radius: 50%;
  animation: pulse 1.5s ease-in-out infinite;
  margin-bottom: 1rem;
}

@keyframes pulse {
  0% { transform: scale(0); opacity: 1; }
  100% { transform: scale(1); opacity: 0; }
}

/* No Data State */
.no-data-state {
  text-align: center;
  padding: 4rem 2rem;
  color: rgba(255, 255, 255, 0.8);
}

.no-data-state h3 {
  color: #ffffff !important;
  margin-bottom: 1rem;
  font-size: 1.5rem;
  font-weight: 700;
}

.no-data-state p {
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 2rem;
  line-height: 1.6;
}

/* Enhanced Typography */
.analytics-page * {
  font-family: 'Inter', system-ui, -apple-system, sans-serif;
}

.analytics-page h1,
.analytics-page h2,
.analytics-page h3,
.analytics-page h4,
.analytics-page h5,
.analytics-page h6 {
  color: #ffffff !important;
  font-weight: 700;
  letter-spacing: -0.025em;
}

.analytics-page p,
.analytics-page span,
.analytics-page div {
  color: rgba(255, 255, 255, 0.9);
}

/* Glass Panel */
.glass-panel {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 2rem;
  margin: 1rem;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}

/* Confidence Intervals Visualization */
.ci-visualization {
  margin: 1rem 0;
}

.ci-bar {
  height: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  position: relative;
  margin-bottom: 0.5rem;
}

.ci-range {
  position: absolute;
  height: 100%;
  background: linear-gradient(90deg, #3b82f6, #1e40af);
  border-radius: 10px;
}

.ci-point {
  position: absolute;
  width: 4px;
  height: 100%;
  background: #ffffff;
  border-radius: 2px;
  transform: translateX(-50%);
}

.ci-labels {
  display: flex;
  justify-content: space-between;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 600;
}

/* ANOVA Table */
.anova-table {
  width: 100%;
}

.anova-row {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  gap: 1rem;
  padding: 0.75rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  font-size: 0.9rem;
}

.anova-row.header {
  font-weight: 700;
  color: #ffffff;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
}

.anova-row span {
  color: rgba(255, 255, 255, 0.9);
}

.anova-row .significant {
  color: #10b981;
  font-weight: 700;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .chart-panel.large {
    grid-column: span 1;
  }
  
  .analytics-grid {
    grid-template-columns: 1fr;
  }
  
  .kpi-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
}

@media (max-width: 768px) {
  .header-main h1 {
    font-size: 2rem;
  }
  
  .summary-header h2 {
    font-size: 1.8rem;
  }
  
  .kpi-card {
    padding: 1.5rem;
  }
  
  .kpi-value {
    font-size: 2rem;
  }
  
  .chart-panel {
    padding: 1rem;
    min-height: 300px;
  }
  
  .panel-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
}

/* Perfect spacing adjustments */
.analytics-page .content-area {
  padding: 1rem;
}

.analytics-grid {
  gap: 1rem;
}

.chart-panel {
  margin-bottom: 0;
}

.kpi-grid {
  gap: 1rem;
  margin-bottom: 1.5rem;
}

